@font-face {font-family: "iconfont";
  src: url(../static/media/iconfont.eot); /* IE9 */
  src: url(../static/media/iconfont.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url(../static/media/iconfont.woff) format('woff'),
  url(../static/media/iconfont.ttf) format('truetype'), 
  url(../static/media/iconfont.svg#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.iconreact:before {
  content: "\e602";
}

